@import './src/assets/scss/breakpoints';
.club-services {
  margin: 10rem 0 14rem;

  &__body {
    display: flex;
    flex-wrap: wrap;
  }

  &__title {
    margin-right: 14rem;
    margin-bottom: 4rem;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    grid-column-gap: 2rem;
    grid-row-gap: 6rem;
    flex: 1 1 auto;
    margin-top: 1.5rem;
  }

  &__item {
    display: flex;
    font-weight: 500;
    font-size: 2.5rem;
    line-height: 125%;
    align-items: center;
    letter-spacing: 0.02em;

    &-svg {
      margin-right: 2rem;
      width: 2.2rem;
      flex: 0 0 2.2rem;
      height: 1.7rem;
      color: var(--color-green);
    }
  }

  @media (max-width: $lg) {
    &__body {
      display: block;
    }
  }

  @media (max-width: $md) {
    margin: 4rem 0 5rem;

    &__items {
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 1.7rem;
    }

    &__item {
      font-size: 1.5rem;

      &-svg {
        width: 1.2rem;
        height: 1rem;
        margin-right: 1.5rem;
      }
    }
  }
}
