@import './src/assets/scss/mixins';
@import './src/assets/scss/breakpoints';

.clubs-item {
  display: flex;
  flex-direction: column;

  &__logo {
    width: 10.9rem;
    height: 2.2rem;
  }

  &__title {
    font-weight: 600;
    font-size: 2rem;
    line-height: 145%;
    text-transform: uppercase;
    margin-bottom: 1.1rem;
  }

  &__address {
    font-weight: 300;
    font-size: 2rem;
    line-height: 125%;
    margin-bottom: 2.3rem;
  }

  &__img {
    margin-top: auto;
    position: relative;
    padding-top: 52%;
    border-radius: 2.5rem;
    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @media (max-width: $md) {
    position: relative;
    min-height: 18rem;
    padding: 2rem 3rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    color: var(--color-white);

    &__address {
      margin-bottom: 0;
    }

    &__img {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      padding-top: 0;
      border-radius: 0;

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--color-black);
        opacity: 0.9;
      }

      .base-new {
        z-index: 2;
      }
    }
  }
}
