.club-price {
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7.5rem;
  }

  &__schedule {
    margin-top: 10rem;
    background: var(--color-green);
    padding: 1.2rem 0;
    width: 100%;

    &-body {
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        width: 1.4rem;
        height: 2.1rem;
      }
    }

    &-link {
      display: inline-flex;
      margin: 0 4rem;
      font-style: italic;
      font-weight: 800;
      font-size: 2.5rem;
      line-height: 125%;
      text-align: center;
      text-transform: uppercase;
    }

  }
}
