@import './src/assets/scss/breakpoints';
@import './src/assets/scss/utils';

.club-trainers {
  margin: 12rem 0;

  &__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6.7rem;
  }

  &__buttons {
    display: flex;
    align-items: center;
  }

  &__btn {
    width: 5.4rem;
    height: 5.4rem;
    background-color: transparentize(#efefef, 0.45);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-right: 3rem;

    svg {
      width: 2.1rem;
      height: 0.9rem;
    }

    &:last-child {
      margin-right: 0;
    }

    &-prev {
      transform: rotate(-180deg);
    }
  }

  &__slider {
    position: relative;
    width: 100%;
  }

  &__slide {
    flex: 0 0 34.7rem;
    margin-right: 3rem;

    display: flex;
    flex-direction: column;

    &:last-child {
      margin-right: 0;
    }
  }

  @media (max-width: $md) {
    margin-top: 4.5rem;
    margin-bottom: 3.6rem;

    .container {
      padding: 0;
    }

    &__wrapper {
      margin: 3rem 3rem 0;
      border-bottom: 2px solid var(--color-black);
      padding-bottom: 3.7rem;
    }

    &__title {
      font-weight: 700;
      font-size: 1.4rem;
      line-height: 1.7rem;
      text-transform: uppercase;
      color: var(--color-orange);
      margin-bottom: 0.1rem;
    }

    &__slider {
      &-sync {
        position: relative;

        &-slide {
          flex: 0 0 100%;
          background: var(--color-white);
          pointer-events: none;

          &.swiper-slide-active {
            pointer-events: auto;
          }
        }

        &-name {
          font-weight: 600;
          font-size: 2rem;
          line-height: 145%;
          text-transform: uppercase;
          margin-bottom: 0.7rem;
        }

        &-text {
          font-weight: 300;
          font-size: 1.4rem;
          line-height: 152.02%;
          margin-bottom: 3rem;
        }
      }
    }

    &__slide {
      flex: 0 0 100%;
      margin-right: 0;
    }
  }
}
