@import './src/assets/scss/breakpoints';
.club-page {
  margin-bottom: 20rem;

  .promo {
    &__inner {
      flex-wrap: wrap;
    }
  }

  @media (max-width: $md) {
    margin-bottom: 8rem;

    .promo {
      display: none;
    }

    &__title {
      font-weight: 600;
      font-size: 2rem;
      line-height: 125%;
      text-transform: uppercase;
      margin-bottom: 2rem;
    }

    &__buttons {
      display: flex;
      justify-content: center;
    }

    &__btn {
      margin-right: 1.8rem;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
