@import './src/assets/scss/breakpoints';
@import '../../assets/scss/breakpoints';

.promo {
  color: white;
  background: var(--color-black);

  margin-bottom: 4rem;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    gap: 2rem;

    padding: 7.1rem 0 4.1rem;
  }

  &__info {
    flex-shrink: 0;
  }

  @media (max-width: $md) {
    margin-bottom: 0;

    &__inner {
      padding: 1rem 0 2rem;
    }
  }

  @media(max-width: $md) {
    margin-bottom: 3rem;

    &__inner {
      padding: .7rem 0 2.3rem;
    }
  }

}
