.price-banner {
  position: relative;
  margin-top: 3rem;
  border-radius: 1.5rem;
  overflow: hidden;
  min-height: 9.5rem;
  display: flex;
  align-items: center;
  justify-content: center;

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    position: relative;
    z-index: 1;
    font-weight: 500;
    line-height: 145%;
    text-align: center;
    text-transform: uppercase;
    color: var(--color-white);

    span {
      margin-top: 0.2rem;
      display: block;
      font-weight: 800;
      font-size: 2.4rem;
      text-transform: uppercase;
    }
  }
}
