@import './src/assets/scss/breakpoints';
.welcome {
  background-color: var(--color-black);
  border-radius: 0 0 13.5rem 13.5rem;
  overflow: hidden;
  padding-top: 20rem;
  margin-top: -20rem;

  &__body {
    position: relative;
    padding: 10rem 0 17.5rem;
  }

  &__title {
    color: var(--color-white);
  }

  &__girl {
    position: absolute;
    bottom: 0;
    right: 24.5rem;
    z-index: 2;
    width: 50.7rem;
  }

  &__man {
    position: absolute;
    bottom: 0;
    z-index: 1;
    right: -17.5rem;
    width: 57.2rem;
  }

  &__bg {
    position: absolute;
    opacity: 0.15;
    border-radius: 50%;
    pointer-events: none;

    &-1 {
      top: 4rem;
      right: -60rem;
      background: radial-gradient(
        50% 50% at 50% 50%,
        #2a8cb3 0%,
        rgba(42, 140, 179, 0) 100%
      );
      transform: rotate(-15deg);
      width: 85.7rem;
      height: 85.7rem;
    }

    &-2 {
      top: 10.5rem;
      right: -38.7rem;
      background: radial-gradient(
        62.64% 32.9% at 51.27% 50.38%,
        #b142cd 0%,
        rgba(42, 140, 179, 0) 100%
      );
      opacity: 0.35;
      width: 129.1rem;
      height: 68.8rem;
      filter: blur(255px);
    }

    &-3 {
      top: -34.5rem;
      right: -60.5rem;
      background: radial-gradient(
        50% 50% at 50% 50%,
        #2a8cb3 0%,
        rgba(42, 140, 179, 0) 100%
      );
      transform: rotate(-15deg);
      width: 137.3rem;
      height: 137.3rem;
    }

    &-4 {
      top: -33rem;
      right: -39rem;
      background: linear-gradient(180deg, #d94bda 0%, #15357c 100%);
      width: 62.6rem;
      height: 62.6rem;
      filter: blur(555px);
    }
  }

  &__visitors {
    margin-bottom: 6.3rem;
    display: flex;
    gap: 4.8rem;
    align-items: center;

    &-img {
      position: relative;
      display: flex;

      img {
        width: 9.4rem;
        height: 9.4rem;
        margin-left: -3.2rem;

        &:first-child {
          margin-left: 0;
        }
      }
    }

    &-plus {
      position: absolute;
      top: 0.3rem;
      right: -1.1rem;
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background: var(--color-white);
      box-shadow: 0 1.1rem 2.5rem rgba(0, 0, 0, 0.75);
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        width: 1rem;
        height: 1rem;
        color: var(--color-purple);
      }
    }

    &-bottom {
      display: flex;
      align-items: center;
      margin-top: 0.8rem;
    }

    &-stars {
      display: flex;
    }

    &-info {
      font-style: italic;
      line-height: 160%;
      color: var(--color-white);
      max-width: 40rem;
    }

    &-stars {
      margin-right: 2.1rem;

      svg {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 0.8rem;
        color: var(--color-orange);
        filter: drop-shadow(var(--box-shadow));

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  &__buttons {
    display: flex;
    gap: 4.4rem;
    align-items: center;
  }

  &__button {
    &-photo {
      display: flex;
      align-items: center;
      gap: 1.7rem;
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 145%;
      text-transform: uppercase;
      color: var(--color-white);

      &:hover {
        .welcome__button-svg {
          svg {
            animation: bounce 1s ease;
          }
        }
      }

      @keyframes bounce {
        20% {
          transform: translateX(0.25rem);
        }

        40% {
          transform: translateX(0);
        }

        60% {
          transform: translateX(0.2rem);
        }

        80% {
          transform: translateX(0);
        }
      }
    }

    &-svg {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 4.7rem;
      height: 4.7rem;
      border-radius: 50%;
      border: 2px solid var(--color-white);

      svg {
        width: 1rem;
        height: 1.3rem;
        color: var(--color-orange);
        filter: drop-shadow(var(--box-shadow));
      }
    }
  }

  @media (max-width: 1450px) {
    &__girl {
      width: 45rem;
      right: 20rem;
    }
  }

  @media (max-width: $xl) {
    &__body {
      padding: 5rem 0 10rem;
    }

    &__man {
      right: -10.5rem;
      width: 46.2rem;
    }

    &__girl {
      width: 35rem;
      right: 17rem;
    }
  }

  @media (max-width: 1250px) {
    border-radius: 0;

    &__body {
      padding: 3rem 0 10rem;
    }

    &__title {
      margin-bottom: 2.5rem;
    }

    &__visitors {
      flex-direction: column;
      align-items: flex-start;
      gap: 2.5rem;
      margin-bottom: 3.5rem;
    }

    &__buttons {
      gap: 3rem;
    }
  }

  @media (max-width: $md) {
    &__body {
      padding: 0.8rem 0 43rem;
    }

    &__title {
      margin-bottom: 2.7rem;
      width: 80%;
    }

    &__visitors {
      flex-direction: row;
      align-items: center;
      margin-bottom: 1.5rem;

      &-img {
        img {
          width: 6.5rem;
          height: 6.5rem;
          margin-left: 0;
        }
      }

      &-plus {
        width: 2.1rem;
        height: 2.1rem;
        right: -0.7rem;
        top: 0.2rem;

        svg {
          width: 0.7rem;
          height: 0.7rem;
        }
      }

      &-info {
        font-weight: 400;
        font-size: 1.4rem;
        line-height: 160%;
      }

      &-bottom {
        font-style: italic;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: 160%;
        color: var(--color-white);
      }
    }

    &__button {
      &-btn {
        position: absolute !important;
        z-index: 5;
        left: 50%;
        transform: translateX(-50%);
        bottom: 6.5rem;
      }
    }

    &__bg {
      right: auto;
      left: 50%;

      &-1 {
        width: 53.5rem;
        height: 53.5rem;
        transform: translateX(-1rem) rotate(-15deg);
        top: 22.6rem;
      }

      &-2 {
        width: 80.7rem;
        height: 43rem;
        transform: translateX(-41rem);
        top: 26.5rem;
      }

      &-3 {
        width: 85.8rem;
        height: 85.8rem;
        transform: translateX(-33rem) rotate(-15deg);
        top: -1.5rem;
      }

      &-4 {
        width: 39.1rem;
        height: 39.1rem;
        transform: translateX(1rem);
        top: -1rem;
      }
    }

    &__girl {
      width: 28rem;
      left: 50%;
      transform: translate(-83%);
    }

    &__man {
      right: 50%;
      transform: translateX(73%);
      width: 32.2rem;
    }
  }
}
