@import "src/assets/scss/mixins";
@import "src/assets/scss/breakpoints";


.contacts {
  &__list {
    @include resetList;

    margin-bottom: 22.8rem;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    gap: 2rem;

    padding-bottom: 4rem;
    border-bottom: .2rem solid var(--color-white-1);
    margin-bottom: 4rem;

    &:hover &-title-svg {
      transform: translateX(0) scale(1);
    }

    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 0;
    }

    &-map {
      flex-shrink: 0;
      flex-basis: 85rem;
      height: 22rem;
    }

    &-content {

    }

    &-title {
      font-weight: 700;
      font-size: 2rem;
      line-height: 145%;

      display: flex;
      align-items: center;
      text-transform: uppercase;

      margin-bottom: 2rem;

      &-svg {
        transform: translateX(-100%) scale(0);
        color: var(--color-green);
        width: 2.9rem;
        height: 2.5rem;

        transition-property: transform;
        transition-duration: var(--duration);
        transition-timing-function: var(--easing);

        margin-left: 1.5rem;
      }
    }

    &-items {
      @include resetList;
    }

    &-item {
      font-size: 1.8rem;
      line-height: 125%;

      margin-bottom: 1.2rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-label {
      font-weight: 500;
      margin-right: .5rem;
    }

    &-value {
      font-weight: 300;
    }

  }

  @media(max-width: $md) {
    margin-bottom: 7rem;

    &__list {
      border-bottom: 0.2rem solid var(--color-white-1);
      margin-bottom: 5rem;
    }

    &__list,
    &__item {
      padding-bottom: 3.3rem;
    }

    &__item {
      display: block;

      margin-bottom: 3rem;

      &-title {
        font-size: 1.6rem;

        margin-bottom: 1.2rem;

        &-svg {
          width: 2rem;
          height: 1.8rem;
        }
      }

      &-item {
        font-size: 1.4rem;
      }

    }

    &__btn {
      &.btn {
        font-size: 1.4rem;
        font-style: normal;

        align-items: flex-start;
      }


      &-svg {
        width: 1.9rem;
        height: 1.9rem;
      }
    }

  }

}
