@import './src/assets/scss/breakpoints';
.clubs-page {
  &__wrapper {
    margin-bottom: 15rem;
  }

  &__ticker {
    margin-bottom: -0.2rem;
  }

  &__map {
    margin: 0;
  }

  @media (max-width: $md) {
    &__wrapper {
      position: relative;
      z-index: 1;
      margin-bottom: -0.2rem;
    }

    .promo {
      margin-bottom: 0;
    }

    &__map {
      .container {
        padding: 0;
      }

      .map {
        border-radius: 0;
      }
    }
  }
}
