@import './src/assets/scss/breakpoints';

.club-top {
  margin-bottom: 10rem;

  &__body {
    display: flex;
    align-items: center;
  }

  &__map {
    margin-right: 7rem;
  }

  &__items {
    flex: 1 0 35rem;
  }

  &__item {
    display: flex;
    margin-bottom: 5rem;

    &:last-child {
      margin-bottom: 0;
    }

    &-svg {
      background: var(--color-white-1);
      border-radius: 50%;
      width: 5.5rem;
      height: 5.5rem;
      flex: 0 0 5.5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 3rem;

      svg {
        width: 1.6rem;
        height: 1.6rem;
        color: var(--color-purple-4);
      }
    }

    &-title {
      font-weight: 600;
      font-size: 2rem;
      line-height: 145%;
      text-transform: uppercase;
      margin-bottom: 0.7rem;
    }

    &-subtitle {
      display: block;
      font-weight: 300;
      font-size: 2rem;
      line-height: 125%;
    }
  }

  @media (max-width: $md) {
    margin-top: 2rem;
    margin-bottom: 4rem;

    &__items {
      flex: 1 1 auto;
      display: flex;
      flex-wrap: wrap;
    }

    &__map {
      display: none;
    }

    &__item {
      margin-bottom: 2.5rem;
      margin-right: 5rem;

      &-svg {
        display: none;
      }

      &-title {
        font-size: 1.2rem;
        margin-bottom: 1.3rem;
      }

      &-subtitle {
        font-size: 1.5rem;
        line-height: 2.2rem;
      }
    }

    &__link-maps {
      font-style: italic;
      font-size: 1.4rem;
      text-decoration: underline;
      color: var(--color-purple);

      margin-top: .4rem;
    }

  }
}
