@import '../../../assets/scss/breakpoints';
.corporate {
  margin: 15rem 0;

  &__body {
    position: relative;
    display: flex;
    padding-top: 5rem;
    margin-bottom: 12rem;
    justify-content: space-between;
  }

  &__left {
    margin-right: 3rem;
    max-width: 70rem;
  }

  &__right {
    display: flex;
    align-items: flex-start;
    padding-right: 12.5rem;

    &-card {
      position: relative;
      z-index: 2;
    }

    &-svg {
      width: 15.3rem;
      height: 15.3rem;
      position: absolute;
      top: 0;
      right: 0;
    }

    &-circle {
      width: 100%;
      height: 100%;
      animation: rotate 7s linear infinite;
    }

    &-arrow {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 5.6rem;
      height: 3.1rem;

      color: var(--color-green);
    }
  }

  &__title {
    margin-bottom: 3rem;
  }

  &__subtitle {
    font-weight: 300;
    line-height: 152.02%;
    margin-bottom: 4rem;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  @media (max-width: 1250px) {
    margin-top: 10rem;

    &__body {
      padding-top: 3.5rem;
    }

    &__right {
      padding-right: 7rem;
      align-items: center;

      &-card {
        width: 40rem;
      }
    }
  }

  @media (max-width: $md) {
    margin: 7.5rem 0;

    &__body {
      display: block;
    }

    &__left {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: none;
      margin-right: 0;
      margin-bottom: 4rem;
    }

    &__title {
      margin-bottom: 1.5rem;
      text-align: center;
    }

    &__subtitle {
      font-size: 1.5rem;
      margin-bottom: 2.5rem;
      text-align: center;
    }

    &__right {
      justify-content: center;
      padding-right: 0;

      &-card {
        width: 100%;
        max-width: 40rem;
      }
    }
  }
}
