.border {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.5rem;
    background: linear-gradient(
      90deg,
      #ffa621 0%,
      #fc714f 51.56%,
      #5c60c9 100%
    );
  }

  @media (max-width: $md) {
    &:before {
      height: 0.3rem;
    }
  }
}
