@import '../../../assets/scss/breakpoints';
.home-clubs {
  margin: 12rem 0 9rem;
  overflow: hidden;

  &__body {
    display: flex;
    align-items: center;
  }

  &__info {
    position: relative;
    z-index: 3;
    flex: 0 0 32rem;
    margin-right: 5rem;

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: 50%;
      transform: translateY(-50%);
      right: -5rem;
      width: 300rem;
      height: 200%;
      background: linear-gradient(
        90deg,
        var(--color-white) 98.5%,
        rgba(239, 246, 248, 0)
      );
    }
  }

  &__title {
    font-weight: 600;
    font-size: 3.5rem;
    line-height: 125%;
    text-transform: uppercase;
    margin-bottom: 2.2rem;
  }

  &__subtitle {
    font-weight: 300;
    line-height: 152.02%;
    margin-bottom: 6rem;
  }

  &__slider {
    width: 1%;
    flex-grow: 1;
    overflow: visible;
    position: relative;
  }

  &__slide {
    flex: 0 0 34.7rem;
    margin-right: 3.2rem;
    border-radius: 2.5rem;
    height: 37rem;
    overflow: hidden;

    &:last-child {
      margin-right: 0;
    }

    &-link {
      display: block;
      position: relative;
      height: 100%;

      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(
          180deg,
          rgba(7, 7, 19, 0) 43.48%,
          rgba(7, 7, 19, 0.9375) 100%
        );

        transform: translateY(1.5rem);
        transition-property: transform;
        transition-duration: var(--duration);
        transition-timing-function: var(--easing);
      }

      @media (hover: hover) {
        &:hover {
          .home-clubs__slide-img {
            transform: scale(1);
          }

          &::before {
            transform: translateY(0);
          }

          .home-clubs__slide-logo,
          .home-clubs__slide-title {
            transform: translateY(-1rem);
          }
        }
      }
    }

    &-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition-property: transform;
      transition-duration: var(--duration);
      transition-timing-function: var(--easing);
      transform: scale(1.05);
    }

    &-content {
      position: absolute;
      z-index: 2;
      bottom: 2.7rem;
      right: 2.8rem;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    &-logo {
      width: 9.7rem;
      height: 1.2rem;
      margin-bottom: 0.5rem;
      transition-property: transform;
      transition-duration: var(--duration);
      transition-timing-function: ease;
    }

    &-title {
      font-weight: 600;
      font-size: 2rem;
      line-height: 145%;
      text-align: right;
      text-transform: uppercase;
      color: var(--color-white);
      transition-property: transform;
      transition-duration: var(--duration);
      transition-timing-function: ease;
    }
  }

  &__btn {
    position: absolute;
    z-index: 5;
    top: 50%;
    transform: translateY(-50%);

    &._desktop {
      left: -3.5rem;

      svg {
        transform: translateX(0.5rem);
      }
    }
  }

  @media (max-width: $md) {
    margin: 6.7rem 0 9.3rem;

    &__body {
      display: block;
    }

    .container {
      padding: 0;
    }

    &__info {
      flex: 1 1 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 4rem;
      margin-right: 0;
      padding: 0 3rem;

      &::before {
        display: none;
      }
    }

    &__title {
      font-size: 2rem;
      text-align: center;
      margin-bottom: 1.5rem;
    }

    &__subtitle {
      font-size: 1.6rem;
      text-align: center;
      margin-bottom: 2.2rem;
    }

    &__slider {
      width: 100%;
      overflow: hidden;
      padding: 0 3rem;
    }

    &__slide {
      height: 35.2rem;
      flex: 0 0 calc(50% - 1.6rem);

      &-link {
        &::before {
          height: 9rem;
          background: var(--color-black);
          opacity: 0.8;
          transform: translateY(0);
        }
      }

      &-new {
        top: auto !important;
        bottom: 2.4rem;
        right: 2rem !important;
      }

      &-content {
        bottom: auto;
        right: 1rem;
        top: 2.6rem;
        left: 3.1rem;
        align-items: flex-start;
        display: block;
      }

      &-title {
        text-align: left;
      }

      &-logo {
        width: 10.1rem;
        height: 1.3rem;
      }
    }

    &__btn {
      top: calc(50% + 1rem);

      &-prev {
        left: -0.6rem;

        svg {
          transform: translateX(1rem) rotate(-180deg) !important;
        }
      }

      &-next {
        right: -0.6rem;

        svg {
          transform: translateX(-1rem);
        }
      }
    }
  }

  @media (max-width: 680px) {
    &__slide {
      flex: 0 0 100%;
    }
  }
}
