@import '../../../assets/scss/breakpoints';

.smart-fit {
  &__body {
    display: flex;
    align-items: center;
    margin-bottom: 7rem;
  }

  &__title {
    font-weight: 500;
    font-size: 2.5rem;
    line-height: 125%;
    margin-bottom: 2.5rem;

    span {
      display: block;
      font-weight: 800;
      font-size: 3.5rem;
      line-height: 125%;
      color: var(--color-green);
    }
  }

  &__text {
    font-weight: 300;
    line-height: 152.02%;
    margin-bottom: 3rem;
  }

  &__left {
    width: 29%;
    margin-right: 10rem;
  }

  &__right {
    position: relative;
    width: 71%;
    min-height: 45rem;
    padding: 6.9rem 0 0 6.2rem;

    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 3.5rem;
    }

    &-girl {
      position: absolute;
      right: 3.5rem;
      bottom: 0;
      width: 35rem;
    }

    &-content {
      position: relative;
      z-index: 1;
      max-width: 31.5rem;
    }

    &-title {
      font-weight: 500;
      font-size: 2rem;
      line-height: 125.5%;
      color: var(--color-white);
      margin-bottom: 2.3rem;
    }

    &-phone {
      font-weight: 800;
      font-size: 2rem;
      line-height: 125.5%;
      color: var(--color-white);
      display: flex;
      align-items: center;
      margin-bottom: 8rem;

      svg {
        width: 2.1rem;
        height: 1.8rem;
        margin-right: 1.6rem;
      }
    }
  }

  &__svg {
    width: 22.8rem;
    height: 11.5rem;
    color: var(--color-white);
  }

  &__slider {
    width: 100%;
  }

  &__slide {
    position: relative;
    flex: 0 0 calc(25% - 2.3rem);
    padding-top: 15%;
    border-radius: 2.5rem;
    overflow: hidden;
    margin-right: 3rem;

    &:last-child {
      margin-right: 0;
    }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &__btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    &-prev {
      left: -0.3rem;
    }

    &-next {
      right: -0.3rem;
    }
  }

  @media (max-width: $lg) {
    &__body {
      display: block;
    }

    &__left {
      width: 100%;
      margin-bottom: 4rem;
    }

    &__right {
      width: 100%;
    }
  }

  @media (max-width: $md) {
    &__top {
      padding-top: 3.7rem;
      background: var(--color-purple-3);
    }

    &__body {
      padding-bottom: 5rem;
    }

    &__left {
      color: var(--color-white);
      width: 70%;
    }

    &__svg {
      margin-bottom: 2.1rem;
    }

    &__title {
      font-size: 2rem;
      margin-bottom: 0.8rem;
    }

    &__text {
      margin-bottom: 2.6rem;
      font-weight: 500;
      font-size: 1.6rem;
    }

    &__more {
      color: var(--color-white) !important;
    }

    &__right {
      padding: 0;
      min-height: auto;
      position: static;

      &-girl {
        width: 25rem;
        right: -6.5rem;
      }
    }
  }

  @media (max-width: 550px) {
    &__body {
      padding-bottom: 0;
      margin-bottom: 0;
    }

    &__left {
      width: 100%;
    }

    &__right {
      position: relative;
      height: 31rem;

      &-bg {
        width: 100%;
        left: 50%;
        transform: translateX(-55%);
        border-radius: 0;

        &._mobile {
          height: 100%;
          display: block !important;
        }
      }
    }
  }

  @media (max-width: 450px) {
    &__right {
      margin-top: -3rem;

      &-bg {
        transform: translateX(-60%);
      }
    }
  }
}
