@import '../../../assets/scss/breakpoints';
@import '../../../assets/scss/utils';
@import '../../../assets/scss/mixins';

.reviews-home {
  margin: 15rem 0 12rem;

  &__quotes {
    width: 3.8rem;
    height: 2.8rem;
    margin: 0 auto 3.4rem;
  }

  &__slider {
    padding: 0 3rem;
  }

  &__slide {
    flex: 0 0 calc(100% - 6rem);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 3rem;

    &-top {
      font-weight: 300;
      font-size: 2rem;
      line-height: 125%;
      display: flex;
      align-items: center;
      margin-bottom: 2.4rem;

      span {
        font-weight: 500;
      }
    }

    &-text {
      font-style: italic;
      font-weight: 300;
      font-size: 2.5rem;
      line-height: 145%;
      text-align: center;
      max-width: 120rem;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6.4rem;

    &-modal {
      @include resetBtn;
      font-weight: 500;
      font-size: 1.4rem;
      text-decoration-line: underline;
      text-transform: uppercase;

      margin: 0 3.5rem;
    }
  }

  &__btn {
    width: 5.4rem;
    height: 5.4rem;
    background-color: transparentize(#efefef, 0.45);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;

    &:last-child {
      margin-right: 0;
    }

    svg {
      width: 2.1rem;
      height: 0.9rem;
    }

    &-prev {
      transform: rotate(-180deg);
    }
  }

  @media (max-width: $md) {
    margin: 7.5rem 0;

    &__quotes {
      margin-bottom: 2.4rem;
    }

    &__slider {
      padding: 0;
    }

    &__buttons {
      & > * {
        margin-right: 2.8rem;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    &__mobile {
      @include resetBtn;
      font-weight: 500;
      font-size: 1.4rem;
      text-decoration-line: underline;
      text-transform: uppercase;
      display: flex;
      margin-top: 4.5rem;
      margin-right: auto;
      margin-left: auto;
      justify-content: center;
      text-align: center;
    }

    &__slide {
      &-top {
        flex-direction: column;
        line-height: 145%;
        font-size: 1.6rem;

        span {
          font-size: 2rem;
        }
      }

      &-text {
        font-size: 1.5rem;
      }
    }

    &__buttons {
      margin-top: 4.4rem;
    }
  }
}
