.mobile-list {
  &__top {
    width: 100%;
    padding: 3rem 0 2rem;
    border-bottom: 2px solid var(--color-black);

    &-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 700;
      font-size: 1.8rem;
      text-transform: uppercase;
      color: var(--color-purple-4);
      text-align: left;
      margin-bottom: 1rem;

      svg {
        width: 1.1rem;
        height: 0.8rem;
        margin-right: 1rem;
        flex: 0 0 1.1rem;
        margin-left: 2rem;

        transition-property: transform;
        transition-duration: var(--duration);
        transition-timing-function: var(--easing);
      }
    }

    &-recruiting {
      font-weight: 700;
      font-size: 1.2rem;
      line-height: 160%;
      text-align: center;
      text-transform: uppercase;
      color: var(--color-white);
      background: var(--color-green);
      border-radius: 1rem;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 3.4rem;
      margin-top: 0.6rem;
    }
  }

  &._show {
    .mobile-list__top {
      svg {
        transform: rotate(-180deg);
      }
    }
  }

  &__wrapper {
    height: 0;
    overflow: hidden;

    transition-property: height;
    transition-duration: var(--duration);
    transition-timing-function: ease;
  }

  &__item {
    padding: 2rem 0 2.8rem;
    border-bottom: 2px solid var(--color-white-1);

    &:last-child {
      border-color: var(--color-black);
    }

    &-text {
      display: block;
      font-style: italic;
      font-size: 1.5rem;
      line-height: 145%;
      text-transform: uppercase;
      margin-bottom: 0.3rem;
    }

    &-price {
      display: inline-flex;
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 145%;
      text-transform: uppercase;

      &._time {
        &::after {
          content: '/';
          margin: 0 0.5rem;
        }

        &:last-child {
          &::after {
            display: none;
          }
        }
      }
    }

    &-sep {
      display: inline-flex;
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 145%;
      text-transform: uppercase;
      margin: 0 0.5rem;

      &:last-child {
        display: none;
      }
    }
  }
}
