@import '../../assets/scss/breakpoints';
.blog-date {
  display: inline-flex;
  align-items: center;
  background: var(--color-black);
  border-radius: 6.9rem;
  font-style: italic;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 135%;
  color: var(--color-white);
  min-height: 5rem;
  padding: 0.5rem 2.5rem;

  svg {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 1.1rem;
  }

  @media (max-width: $md) {
    font-size: 1.2rem;
    padding: 0.5rem 1.9rem;
    min-height: 4.2rem;
  }
}
