@import './src/assets/scss/breakpoints';

.list-title {
  display: flex;

  &__item {
    display: flex;
    margin-right: 5rem;
    font-style: italic;
    font-weight: 700;
    font-size: 1.8rem;
    line-height: 2.2rem;
    align-items: center;
    text-transform: uppercase;

    svg {
      width: 2.5rem;
      height: 2.3rem;
      margin-right: 1.3rem;
    }

    &._blue {
      color: var(--color-purple-2);
    }

    &._purple {
      color: var(--color-pink);
    }

    &._red {
      color: var(--color-light-red);
    }
  }

  @media (max-width: $md) {
    flex-wrap: wrap;

    &__item {
      color: var(--color-blue) !important;
      margin-right: 2.5rem;
      margin-bottom: 1.1rem;
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 1.7rem;

      &:nth-child(1) {
        order: 3;
      }

      &:nth-child(2) {
        order: 2;
      }

      &:nth-child(3) {
        order: 1;
      }

      svg {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 1rem;
      }
    }
  }
}
