@import '../../assets/scss/breakpoints';

.header {
  background: var(--color-black);
  padding: 4.3rem 0;

  &__body {
    display: flex;
    align-items: center;
  }

  &__logo {
    margin-right: 3.2rem;

    svg {
      width: 14.4rem;
      height: 6.4rem;
    }
  }

  &__text {
    color: var(--color-grey);
    font-weight: 600;
    font-size: 1.4rem;
    line-height: 160%;
    text-transform: uppercase;

    span {
      display: block;
      text-align: right;
    }

    &._mw {
      max-width: 17rem;
      padding-top: .7rem;
    }

    &._ml {
      margin-left: 2rem;
    }
  }

  &__nav {
    position: relative;
    z-index: 5;
    display: flex;
    gap: 5rem;
    margin-left: 9rem;
    margin-right: auto;
  }

  &__link {
    position: relative;
    color: var(--color-white);
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.7rem;
    text-transform: uppercase;

    transition-property: color;
    transition-duration: var(--duration);
    transition-timing-function: var(--easing);

    &::before {
      content: '';
      position: absolute;
      width: 0.5rem;
      height: 0.5rem;
      top: -1.7rem;
      left: 50%;
      transform: translateX(-50%) scale(0);
      background: var(--color-orange);
      border-radius: 50%;
      opacity: 0;
      box-shadow: var(--box-shadow);

      transition-property: opacity, transform;
      transition-duration: var(--duration);
      transition-timing-function: var(--easing);
    }

    &:hover {
      color: var(--color-orange);
    }

    &.active {
      color: var(--color-orange);
      cursor: default;

      &::before {
        opacity: 1;
        transform: translateX(-50%) scale(1);
      }
    }
  }

  &__burger {
    width: 5.5rem;
    height: 5.5rem;
    background-color: transparentize(#263f71, 0.75);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      color: var(--color-purple-2);
      width: 2.3rem;
      height: 1.6rem;
    }
  }

  @media (max-width: $xl) {
    &__nav {
      margin-left: 5rem;
    }
  }

  @media (max-width: $lg) {
    &__logo {
      margin-right: 0;
    }
  }

  @media (max-width: $md) {
    padding: 3rem 0 3.5rem;

    &__body {
      justify-content: space-between;
    }
  }
}
